<template>
<div>
  <svg @click="clickChangeBack()" xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>{{title}}</title>
      <g fill="#000000">
          <path d="M14.22 17.05l-2.83-2.83c-1.8 2.32-2.91 5-3.25 7.78h4.04c.29-1.75.97-3.44 2.04-4.95zM12.18 26H8.14c.34 2.78 1.45 5.46 3.25 7.78l2.83-2.83c-1.07-1.51-1.75-3.2-2.04-4.95zm2.02 10.63c2.32 1.81 5.02 2.88 7.8 3.22v-4.04c-1.75-.29-3.43-.98-4.93-2.05l-2.87 2.87zM26 8.14V2l-9.1 9.1L26 20v-7.82c5.67.95 10 5.88 10 11.82s-4.33 10.87-10 11.82v4.04c7.89-.99 14-7.7 14-15.86S33.89 9.13 26 8.14z"/>
      </g>
  </svg>
</div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: 'rotate left'
    }
  },
  data () {
    return {
      indexChange: -1
    }
  },
  methods: {
    clickChangeBack () {
      const app = this.$parent
      if (this.indexChange > -1) {
        if (this.indexChange > 0) {
          this.indexChange--
        }
      } else {
        this.indexChange = app.todolistChange.length - 2
      }
      app.todolistChangeActive = false
      app.todolist = JSON.parse(JSON.stringify(app.todolistChange[this.indexChange]))
      this.$nextTick(() => {
        app.todolistChangeActive = true
      })
    }
  }
}
</script>
