<template>
<div>
  <svg @click="clickChangeNext()" xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>{{title}}</title>
      <g fill="#000000">
          <path d="M31.1 11.1L22 2v6.14C14.11 9.12 8 15.84 8 24s6.11 14.88 14 15.86v-4.04c-5.67-.95-10-5.88-10-11.82s4.33-10.87 10-11.82V20l9.1-8.9zM39.86 22c-.34-2.78-1.45-5.46-3.25-7.78l-2.83 2.83c1.07 1.51 1.75 3.2 2.04 4.95h4.04zM26 35.81v4.05c2.78-.34 5.48-1.42 7.8-3.22l-2.87-2.87c-1.5 1.06-3.18 1.74-4.93 2.04zm7.78-4.86l2.83 2.83c1.8-2.32 2.91-5 3.25-7.78h-4.04c-.29 1.75-.97 3.44-2.04 4.95z"/>
      </g>
  </svg>
</div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: 'rotate right'
    }
  },
  methods: {
    clickChangeNext () {
      const app = this.$parent
      if (app.$children[1].indexChange < app.todolistChange.length - 1) {
        app.$children[1].indexChange++
        app.todolistChangeActive = false
        app.todolist = JSON.parse(JSON.stringify(app.todolistChange[app.$children[1].indexChange]))
        this.$nextTick(() => {
          app.todolistChangeActive = true
        })
      }
    }
  }
}
</script>
